import C from "./canvas";
import { CELL } from "./constant";

type CellProps = {
  position: { x: number; y: number };
  width?: number;
  height?: number;
};

class Cell {
  position: CellProps["position"];
  width: CellProps["width"];
  height: CellProps["height"];

  constructor(props: CellProps) {
    const { position, width, height } = props;
    this.position = position;
    this.width = width || CELL.WIDTH;
    this.height = height || CELL.HEIGHT;
  }

  draw() {
    C.fillStyle = "blue";
    C.fillRect(this.position.x, this.position.y, this.width, this.height);
  }
}

function setMap(map: number[][]) {
  const boundaries: Cell[] = [];
  map.forEach((row, i) => {
    row.forEach((cell, j) => {
      if (cell !== 1) return;
      const position = {
        y: CELL.HEIGHT * i,
        x: CELL.WIDTH * j,
      };
      boundaries.push(new Cell({ position }));
    });
  });
  return boundaries;
}

export { setMap };
export default Cell;
